:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

body {
  margin: 0;
}

ul {
  list-style: none;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 2px 4px 0 #0000000d;

  padding-inline: 50px;
}

/* .logo {} */

/* .nav {} */

.nav__list {
  display: flex;
  gap: 20px;

  list-style: none;

  margin: 0;
  padding: 0;
}

/* .nav__item {} */

.nav__link {
  display: flex;
  align-items: center;

  position: relative;

  height: 60px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  font: inherit;
}

.nav__link:hover,
.is-active {
  color: var(--main-color);
}

.is-active::after {
  content: '';

  display: block;
  position: absolute;

  left: 0;
  right: 0;
  bottom: 0;

  height: 4px;
  background-color: var(--main-color);
  border-radius: 8px;
}
